body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
}
.App__content {
  width: 75%;
}
* {
  margin: 0;
  padding: 0;
}

.header {
  border-bottom: 1px solid black;
  width: 100%;
  height: 52px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
}
.header__left {
  font-size: 30px;
  padding-bottom: 6px;
}
.header__left-home {
  color: grey;
  margin-right: 10px;
  text-decoration: none;
}
.header__left-home:hover {
  color: darkgrey;
}
.header__left-home:visited {
  text-decoration: none;
}
.header__left-portfolio {
  color: grey;
  margin-right: 10px;
}
.header__left-portfolio:hover {
  color: darkgrey;
}
.header__left-portfolio:visited {
  text-decoration: none;
}
.header__left-about {
  color: grey;
  margin-right: 10px;
}
.header__left-about:hover {
  color: darkgrey;
}
.header__left-contact {
  color: grey;
  margin-right: 10px;
}
.header__left-contact:hover {
  color: darkgrey;
}
.header__center {
  font-size: 24px;
  font-family: "Poppins", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}

.home {
  height: 100%;
}
.home__content {
  height: 94.213%;
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  text-align: center;
}
.home__content-landing {
  width: 50%;
}
.home__content-landing .title {
  text-align: center;
}
.home__content-landing .text-wrapper .text1 {
  font-size: 32px;
  opacity: 0;
  -webkit-animation: an1 6s infinite;
          animation: an1 6s infinite;
  text-align: center;
}
.home__content-landing .text-wrapper .text2 {
  font-size: 32px;
  opacity: 0;
  -webkit-animation: an2 6s infinite;
          animation: an2 6s infinite;
  position: absolute;
  text-align: center;
}
.home__content-landing .text-wrapper .text3 {
  font-size: 32px;
  opacity: 0;
  -webkit-animation: an3 6s infinite;
          animation: an3 6s infinite;
  position: absolute;
  text-align: center;
}
@-webkit-keyframes an1 {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
}
@keyframes an1 {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
}
@-webkit-keyframes an2 {
  30% {
    opacity: 0;
  }
  45% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
}
@keyframes an2 {
  30% {
    opacity: 0;
  }
  45% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
}
@-webkit-keyframes an3 {
  60% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
}
@keyframes an3 {
  60% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
}
.home__content-landing .end {
  text-align: center;
}
